<template>
    <div class="update">
        <navigate title="更新" position="absolute"/>
        <div class="scroll-wrapper">
            <div class="box">
                <div class="icon-wrapper">
                    <mu-icon value="arrow_upward" size="70" color="#fff"></mu-icon>
                </div>
                <div class="version-status">{{tip}}</div>
                <div class="version">{{version}}</div>
            </div>
            <mu-expand-transition>
                <div class="new-version" v-if="news.visible">
                    <div class="header">
                        <span clas="title">发现新版本</span>
                        <div class="btn">
                            <mu-button flat class="ignore-btn" @click="news.visible = false">忽略</mu-button>
                            <mu-button flat class="version-btn" color="success" @click="downloadApk">升级</mu-button>
                        </div>
                    </div>
                    <ul style="padding-bottom: 20px">
                        <li>版本：{{news.version}}</li>
                        <li>大小：{{news.file.size}}M</li>
                        <li>更新时间：{{news.uploadTime}}</li>
                        <li>更新内容：
                            {{news.uploadContent}}
                        </li>
                    </ul>
                </div>
            </mu-expand-transition>
            <div class="button-wrapper" v-if="!news.visible">
                <mu-button class="button" color="#fff" @click="checkUpdate" flat>检查更新</mu-button>
            </div>

            <!-- dialog -->
            <mu-dialog :open.sync="progress.visible" width="80%" lock-scroll>
                <mu-sub-header>正在下载...</mu-sub-header>
                <br/>
                <m-progress :progress="progress.value" :max="progress.max"/>
                <br/>
            </mu-dialog>
            <!-- dialog -->
        </div>
    </div>
</template>

<script>
    import Navigate from "../../../base/navigate/navigate"
    import MProgress from "../../../base/progress/progress"
    import {appMixins} from "../../../common/mixins"
    import {checkUpdate} from "../../../api/sys"
    import {installationApkfile} from "../../../common/js/cordova/map"
    import {downloadFile, getSavePath} from "../../../common/js/cordova/download"

    export default {
        name: "update",
        mixins: [appMixins],
        data() {
            return {
                tip: '已经是最新版本',
                news: {
                    visible: false,
                    version: '0.0.0',
                    file: {
                        size: 0,
                        url: '',
                        savePath: ''
                    },
                    uploadTime: '',
                    uploadContent: '',
                },
                progress: {
                    visible: false,
                    value: 0,
                    max: 100
                }
            }
        },
        methods: {
            checkUpdate() {
                this.$toast({
                    position: 'center',
                    type: 'loading',
                    message: '正在查询 ……',
                    duration: 0
                })

                checkUpdate(this.version).then(res => {
                    if (res) {
                        this.news.visible = true
                        this.news.version = res.version
                        this.news.file.size = (res.file_size / 1024 / 1024).toFixed(2)
                        this.news.file.url = res.file_url
                        this.news.uploadTime = res.upload_time
                        this.news.uploadContent = res.detail
                        this.tip = '发现新版本'
                    } else {
                        this.tip = '已经是最新版本'
                    }

                    this.$toast.clear()
                })
            },
            downloadApk() {
                this.progress.visible = true
                // this.news.file.url = this.$images + this.news.file.url
                if (!this.news.file.url) {
                    return this.$toast('无效下载地址')
                }

                console.log(`%c新版本下载地址: ${this.news.file.url}`, 'color: #9c27b0')

                this.news.file.savePath = getSavePath() + this.version + ".apk"

                downloadFile(this.news.file.url, this.news.file.savePath, success => {
                    let data = JSON.parse(success.toString())

                    //下载完成安装apk
                    if (data.type === "completed") {
                        this.progress.visible = false
                        this.news.visible = false

                        installationApkfile(this.news.file.savePath)
                    }

                    if (data.type === "progress") {
                        this.progress.value = Number.parseInt(data.soFarBytes)
                        this.progress.max = Number.parseInt(data.totalBytes)
                    }
                }, console.log)
            }
        },
        components: {
            MProgress,
            Navigate
        }
    }
</script>

<style lang="stylus" scoped>
    @import "../../../common/stylus/variable.styl"

    .update
        padding-top 46px
        background-color: #efefef
        .scroll-wrapper
            padding-top 15px
            width 100%
            height 100%
            .box
                display flex
                flex-direction column
                align-items center
                justify-content center
                background-color #fff
                padding 20px 0
                .icon-wrapper
                    margin 30px 0 20px
                    width 70px
                    height 70px
                    background-color #00A0EA
                    border-radius 50%
                    overflow hidden
                .version-status
                    margin-bottom 15px
                    font-size $font-size-ml
                    color #353535
                .version
                    font-size $font-size-ms
                    margin-bottom 20px
            .new-version
                width 100%
                padding 0 20px
                line-height 26px
                background-color #fff
                .header
                    height: 45px;
                    line-height: 40px;
                    margin-bottom: 10px;
                    border-bottom: 1px solid #d6d6d6;
                    color #333;
                    padding-top: 0px;
                    background-color: #fff
                    .title
                        float left
                    .btn
                        float right
                        .mu-flat-button
                            min-width 40px
                            border-radius: 4px;
                            &.ignore-btn
                                color #9C9C9C;
                            &.version-btn
                                border: 1px solid #4caf50;
                ul
                    color #9C9C9C

            .button-wrapper
                padding 15px
                .button
                    width 100%
                    background-color #00A0EA
</style>



