/**
 * 下载文件
 * @param fileUrl
 * @param savePath
 * @param successCallback
 * @param errorCallback
 * @returns {Promise<any>}
 */
export function downloadFile(fileUrl, savePath, successCallback, errorCallback) {
    if (!window.Arcgis || typeof window.Arcgis.downloadFile !== 'function') {
        return Promise.reject('没有找到下载文件方法')
    }

    window.Arcgis.downloadFile(fileUrl, savePath, successCallback, errorCallback)
}

/**
 * 下载暂停
 * @param id
 * @returns {Promise<any>}
 */
export function downloadFilePause(id) {
    if (!window.Arcgis || typeof window.Arcgis.downloadFilePause !== 'function') {
        return Promise.reject('没有找到下载暂停方法')
    }

    window.Arcgis.downloadFilePause(id)
}

/**
 * 下载状态
 * @param fileUrl
 * @param savePath
 * @param successCallback
 * @param errorCallback
 * @returns {Promise<any>}
 */
export function downloadFileSatus(fileUrl, savePath, successCallback) {
    console.log("fileSatus")
    if (!window.Arcgis || typeof window.Arcgis.downloadFileSatus !== 'function') {
        return Promise.reject('没有找到获取下载状态方法')
    }

    window.Arcgis.downloadFileSatus(fileUrl, savePath, successCallback)
}

/**
 * 删除文件
 * @param fileUrl
 * @param savePath
 * @param successCallback
 * @param errorCallback
 * @returns {Promise<any>}
 */
export function downloadFileClear(fileUrl, savePath, successCallback, errorCallback) {
    if (!window.Arcgis || typeof window.Arcgis.downloadFileClear !== 'function') {
        return Promise.reject('没有找到删除文件方法')
    }

    window.Arcgis.downloadFileClear(fileUrl, savePath, successCallback, errorCallback)
}

/**
 * 获取设备文件存储基本路径
 * @returns {Promise<any>}
 */
export function getDeviceBaseFilePath() {
    if (!window.cordova) {
        return Promise.reject('没有找到cordova对象')
    }
    const filePath = window.cordova.file.externalRootDirectory
    return filePath.toString().replace("file://", "")
}

/**
 * 获取文件存储路径
 * @returns {Promise<any>}
 */
export function getSavePath() {
    return getDeviceBaseFilePath() + 'gdhdt/map/'
}



